.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
}

.nav {
  display: flex; 
  align-items: center;
  a {
    font-size: 20px;
    line-height: 30px;
    font-weight: normal;
    padding: 25px 32px;
    display: block;
  }
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 767px) {
  .container {
    padding: 0 0;
  }
  .header{
    flex-direction: column;
    align-items: center;
  }
  .logo{
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .nav {
    width: 100%;
    flex-wrap: wrap;
    li {
      width: 50%;
      border: 1px solid #ffffff;
      text-align: center;
    }
    a {
      font-size: 16px;
      line-height: 24px;
      padding: 12px 32px;
    }
  }
}