/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,900&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  vertical-align: bottom;
  max-width: 100%;
  height: auto; }

*,
*:before,
*:after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: initial; }

body {
  font-family: 'Roboto', 'Noto Sans TC', sans-serif; }

.white, .white * {
  color: #ffffff; }

.bg-main {
  background-color: #AA0601; }

.flex {
  display: flex; }

.flex-sb {
  justify-content: space-between; }

.align-item-center {
  align-items: center; }

.w-80 {
  width: 80px; }

.h-80 {
  height: 80px; }

.py-25 {
  padding-top: 25px;
  padding-bottom: 25px; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px; }

.nav {
  display: flex;
  align-items: center; }
  .nav a {
    font-size: 20px;
    line-height: 30px;
    font-weight: normal;
    padding: 25px 32px;
    display: block; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center; }

@media (max-width: 767px) {
  .container {
    padding: 0 0; }
  .header {
    flex-direction: column;
    align-items: center; }
  .logo {
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .nav {
    width: 100%;
    flex-wrap: wrap; }
    .nav li {
      width: 50%;
      border: 1px solid #ffffff;
      text-align: center; }
    .nav a {
      font-size: 16px;
      line-height: 24px;
      padding: 12px 32px; } }
